body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	
  background: #252525;
  color: #eeeeee;
}

.detail {
  background: #353535;
}

.tab-controls{
  color: white;
  background: #353535;
}

.tab-controls:hover {
  color: white;
}


.controls {
  background: #353535;
}

.loading {
  background: rgb(0,0,0);
  opacity: 0.85;
  width: 90%; 
  height: 90%;
  position: fixed;
  top: 5%;
  left: 5%;
  z-index: 2;
  text-align: center;
  padding-top: 20%
}

.loading-small {
  background: rgb(0,0,0);
  opacity: 0.85;
  width: 50%; 
  height: 50%;
  position: fixed;
  top: 25%;
  left: 25%;
  z-index: 2;
  text-align: center;
  padding-top: 10%
}

.custom-modal {
  background: #353535;
}